.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.header {
  width: 100%;
  background-color: #3E516B;
  display: -ms-flexbox;
  display: flex;
  padding: 0 1em;
  color: #AFD5DE;
}

.header_content {
  margin: 0 auto;
  width: 1000px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
