ul {
  list-style-type: none;
  margin: 0;
  padding: 0 0 0 10px;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  -ms-flex-align: center;
      align-items: center;
}

ul>*:first-child {
  margin-right: 40px;
}

li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  text-align: center;
  font-weight: bold;
